@mixin st_btn() {
	display: inline-flex;
	justify-content: center;
	align-items: center;

	text-align: center;
	white-space: nowrap;
	font-family: $biotif_font;

	cursor: pointer;

	&:hover,
	&:focus {
		text-decoration: none;
	}

	[class$="_title"] {
		&:not(:last-child) {
			margin-right: 2rem;
		}
	}
}

// button size extends
%btn_size_1,
%btn_size_2 {
	letter-spacing: -.01em;
}

%btn_size_1 {
	font-size: 2.4rem;

	border-radius: 4.2rem;

	@include bp($point_2, min) {
		min-width: 28.2rem;
		min-height: 8.2rem;

		padding: 0 2rem 0 4rem;

		line-height: 8.2rem;
	}
	@include bp($point_3, $point_4) {
    min-width: 21.5rem;
		min-height: 7.2rem;

		line-height: 7.2rem;
  }
	@include bp($point_3) {
		padding: 0 1.2rem 0 3.2rem;
	}
	@include bp($point_4 - 1) {
    min-width: 19.6rem;
		min-height: 5.4rem;

		line-height: 5.4rem;
		font-size: 1.6rem;
  }
}

%btn_size_2 {
	border-radius: 3rem;

	@include bp($point_2, min) {
		padding: 0 4rem;
		min-width: 15.6rem;
		min-height: 6rem;
	
		font-size: 2.4rem;
		line-height: 6rem;	
	}

	@include bp($point_3, $point_4) {
		min-width: 15rem;
	}

	@include bp($point_3) {
		padding: 0 3.2rem;
		min-height: 5.4rem;

		line-height: 5.4rem;
		font-size: 1.6rem;
	}

	@include bp($point_4 - 1) {
		min-width: 14rem;
	}
}

// button color extends
%btn_theme_1,
%btn_theme_2,
%btn_theme_3,
%btn_theme_4,
%btn_theme_5,
%btn_theme_6 {
	transition: background-color .3s ease, color .3s ease;

	@include st_btn();
}

%btn_theme_1 {
	color: var(--primary_dark_color);

	background-color: rgba(#EEF3F9, .75);

	&:hover {
		color: var(--white_color);

		background-color: var(--primary_light_color);
	}
}

%btn_theme_2 {
	color: var(--white_color);

	background-color: var(--primary_light_color);

	&:hover {
		color: var(--white_color);

		background-color: var(--primary_color);
	}
}

%btn_theme_3 {
	color: var(--white_color);

	background-color: rgba(#32CE64, .75);

	&:hover {
		color: var(--primary_color);

		background-color: var(--white_color);
	}
}

%btn_theme_4 {
	color: var(--white_color);

	background-color: var(--blue_color);

	&:hover {
		color: var(--primary_color);

		background-color: var(--white_color);
	}
}

%btn_theme_5 {
	color: var(--primary_dark_color);

	background-color: var(--white_color);

	&:hover {
		color: var(--white_color);

		background-color: var(--primary_light_color);
	}
}

%btn_theme_6 {
	color: var(--primary_color);

	background-color: var(--gray_3_color);

	&:hover {
		color: var(--gray_3_color);

		background-color: var(--primary_color);
	}
}

// icon size extends
%btn_icon_size_1 {
	margin-left: auto;

	border-radius: 50%;

	@include bp($point_4, min) {
		padding: 1.6rem 1.6rem 1.6rem 1.8rem;
		width: 4.8rem;
		height: 4.8rem;
  }
	@include bp($point_4 - 1) {
    width: 3rem;
		height: 3rem;
		padding: 1rem;
  }
}

// icon color extedns
%btn_icon_theme_1 {
	color: var(--primary_dark_color);

	background-color: var(--white_color);
}

%btn_icon_theme_2 {
	color: var(--white_color);

	background-color: var(--primary_dark_color);
}