@import "ScssHelpers/_helpers_all.scss";

.dev_partner_cube {
	position: relative;
	z-index: 1;

	@include bp($point_2, min) {
		padding: 4.2rem 10.4rem 4.2rem 6rem;
	}

	@include bp($point_3, $point_4) {
		padding: 3.8rem 6.4rem;
	}

	@include bp($point_4 - 1) {
		padding: 1.8rem 2.5rem 1.8rem 3rem;
	}

	// &.dev_partner_cube--theme_orange_mod {
	// 	background-color: var(--orange_color);
	// }

	// &.dev_partner_cube--theme_white_mod {
	// 	background-color: var(--white_color);
	// }

	// &.dev_partner_cube--theme_primary_dark_mod {
	// 	background-color: var(--primary_dark_color);
	// }

	// &.dev_partner_cube--theme_primary_light_mod {
	// 	background-color: var(--primary_light_color);
	// }
}

.dev_partner_cube__icon_wrap {
	@include bp($point_2, min) {
		width: 10rem;
		height: 10rem;

		&:not(:last-child) {
			margin-bottom: 3.2rem;
		}
	}

	@include bp($point_3, $point_4) {
		width: 14rem;
		height: 14rem;

		&:not(:last-child) {
			margin-bottom: 4rem;
		}
	}

	@include bp($point_4 - 1) {
		$size: calc(.17 * 100vw + 1rem);

		width: $size;
		height: $size;

		&:not(:last-child) {
			margin-bottom: 1.6rem;
		}
	}
}

.dev_partner_cube__number,
.dev_partner_cube__label {

	@include bp($point_3, $point_4) {
		font-size: 2.8rem;
	}

	@include bp($point_4 - 1) {
		font-size: calc(.0313 * 100vw + 4px);
	}

	.dev_partner_cube--theme_orange_mod &,
	.dev_partner_cube--theme_primary_dark_mod & {
		color: var(--white_color);
	}

	.dev_partner_cube--theme_white_mod & {
		color: var(--primary_color);
	}
}

.dev_partner_cube__number {
	font-weight: 700;
	letter-spacing: -.03em;

	@include bp($point_2, min) {
		margin-bottom: -1rem;
		
		font-size: 5.2rem;
		line-height: 1;
	}

	@include bp($point_3) {
		display: inline;
		line-height: 1.3;
	}
}

.dev_partner_cube__label {
	@include bp($point_2, min) {
		font-size: 2rem;

		line-height: 1.45;
	}

	@include bp($point_3) {
		display: inline;

		font-weight: 700;
		line-height: 1.3;
		text-transform: lowercase;
	}
}

.dev_partner_cube__img_wrap {
	position: absolute;
	// top: 0;
	// left: 0;
	top: 0;
	bottom: -1px;
	right: -1px;
	left: 0;

	// width: 100%;
	// height: 100%;

	z-index: -1;

	overflow: hidden;
	@include d3();
}

.dev_partner_cube__img_wrap_in {
	width: 100%;
	height: 100%;
	@include d3();

	.dev_partner_cube--theme_orange_mod & {
		background-color: var(--orange_color);
	}

	.dev_partner_cube--theme_white_mod & {
		background-color: var(--white_color);
	}

	.dev_partner_cube--theme_primary_dark_mod & {
		background-color: var(--primary_dark_color);
	}

	.dev_partner_cube--theme_primary_light_mod & {
		background-color: var(--primary_light_color);
	}
}

.dev_patner_cube__picture {
	display: block;
	width: 100%;
	height: 100%;
}

.dev_patner_cube__img {
	@extend %cover_img;
}