@import "ScssHelpers/helpers_all";

.showcase {
	padding: 0;
}

.showcase__section_title {
	@extend %h1;

	position: relative;

	position: absolute;
	left: var(--gl-indent);

	// font-weight: 700;
	color: var(--white_color);
	letter-spacing: -0.044em;

	z-index: 3;

	span {
		font-family: $font_2;
	}

	@include bp($point_2, min) {
		// letter-spacing: -.3rem;
		top: 13rem;

		// font-size: 14rem;
		// line-height: 1.1;
	}

	@include bp($point_3, $point_4) {
		top: 8.2rem;

		// font-size: 6.4rem;
		// line-height: .9;
		// letter-spacing: -.2rem
	}

	@include bp($point_4 - 1) {
		top: 6rem;

		// font-size: 4.7rem;
		// line-height: .9;
		// letter-spacing: -.1rem
	}
}

.showcase__slider {
	@include bp($point_2, min) {
		min-height: 90rem;
		height: 100vh;
	}

	@include bp($point_3, $point_4) {
		height: 110.4rem;
	}

	@include bp($point_4 - 1) {
		height: 67rem;
	}

	.swiper-wrapper {
		position: absolute !important;
		top: 0;
		left: 0;

		width: 100% !important;
		height: 100%;

		transform: none !important;
	}
}

.showcase__item {
	@extend %global_flex_block_row_nowrap_flex-start_stretch;

	// height: auto;

	&.swiper-slide {
		position: absolute !important;
		top: 0 !important;

		display: flex !important;
		height: 100% !important;
		width: 100% !important;
		min-height: 100% !important;

		will-change: transform;
		float: none !important;
		pointer-events: none;
	}

	&.swiper-slide-active {
		// z-index: 1;
		pointer-events: auto;
	}

	&:first-child {
		z-index: 1;
	}
}

.showcase__col {
	@extend %global_flex_block_row_nowrap_flex-start;
	position: relative;

	overflow: hidden;

	&:nth-child(1) {
		@include bp($point_2, min) {
			padding: 20.4rem var(--gl-indent) 6rem;
		}

		@include bp($point_3, $point_4) {
			padding: 17.6rem var(--gl-indent) 15.8rem;
		}

		@include bp($point_4 - 1) {
			padding: 14rem var(--gl-indent) 10.4rem;
		}
	}

	&:nth-child(2) {
		padding: 0 8.2rem;

		@include bp($point_3) {
			display: none;
		}
	}

	@include bp($point_2, min) {
		width: 50%;
		min-height: 100vh;
	}

	@include bp($point_3) {
		width: 100%;
	}
}

.showcase__bg {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;

	z-index: -1;

	@include d3();

	&.showcase__bg--v1 {
		// top: 100%;

		.showcase__item:nth-child(1) & {
			background-color: #5d8bc1;
		}
		.showcase__item:nth-child(2) & {
			background-color: #5767ad;
		}
		.showcase__item:nth-child(3) & {
			background-color: #1a1a1a;
		}
		.showcase__item:nth-child(4) & {
			background-color: #ed9c25;
		}
		.showcase__item:nth-child(5) & {
			background-color: #39ac68;
		}
		.showcase__item:nth-child(6) & {
			background-color: #473589;
		}
		.showcase__item:nth-child(7) & {
			background-color: #1b2d4d;
		}
		.showcase__item:nth-child(8) & {
			background-color: #111826;
		}
		.showcase__item:nth-child(9) & {
			background-color: #e04f58;
		}
	}

	&.showcase__bg--v2 {
		// top: -100%;

		.showcase__item:nth-child(1) & {
			background-color: #4b7fbc;
		}
		.showcase__item:nth-child(2) & {
			background-color: #4d5ea7;
		}
		.showcase__item:nth-child(3) & {
			background-color: #111212;
		}
		.showcase__item:nth-child(4) & {
			background-color: #e28f16;
		}
		.showcase__item:nth-child(5) & {
			background-color: #28a058;
		}
		.showcase__item:nth-child(6) & {
			background-color: #3d2a81;
		}
		.showcase__item:nth-child(7) & {
			background-color: #132646;
		}
		.showcase__item:nth-child(8) & {
			background-color: #0c1119;
		}
		.showcase__item:nth-child(9) & {
			background-color: #c93944;
		}
	}
}

.showcase__content {
	@include bp($point_2, min) {
		max-width: 49rem;
		margin: auto 0;
	}

	@include bp($point_3) {
		width: 100%;
	}
}

.showcase__content_in {
	@include bp($point_2, min) {
		@include d3();

		margin-bottom: 7.8rem;
	}

	@include bp($point_3, $point_4) {
		margin-bottom: 4.4rem;
	}
}

.showcase__tag {
	margin-bottom: 1.6rem;

	color: var(--white_color);

	@include bp($point_2, min) {
		font-size: 1.8rem;
	}

	@include bp($point_3, $point_4) {
		font-size: 1.4rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1.3rem;
	}
}

.showcase__content_header {
	@include bp($point_3) {
		@include d3();
	}

	@include bp($point_2, min) {
		margin-bottom: 2rem;
		// height: 10.2rem;
		height: 14.2rem;
	}

	@include bp($point_3, $point_4) {
		margin-bottom: 1rem;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 1.8rem;
	}
}

.showcase__title {
	display: block;

	line-height: 1.2;
	font-weight: 600;
	color: var(--white_color);
	transition: color 0.3s;

	overflow: hidden;

	&:hover,
	&:focus {
		color: #1da0df;
		text-decoration: none;
	}

	@include bp($point_2, min) {
		font-size: 4.8rem;
	}

	@include bp($point_3, $point_4) {
		font-size: 4rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1.9rem;
		line-height: 1.2;
	}
}

.showcase__text {
	@extend %base_text_1;

	color: var(--white_color);
	overflow: hidden;

	@include bp($point_2, min) {
		height: 12rem;
	}

	@include bp($point_3) {
		@include d3();
	}

	@include bp($point_3, $point_4) {
		height: auto;
		max-width: 59rem;

		font-size: 1.8rem;
		line-height: 1.5;
	}

	@include bp($point_4 - 1) {
		max-height: 7.6rem;
		margin: 0;
		font-size: 1.4rem;
	}
}

.showcase__btn_wrap {
	position: relative;
	z-index: 3;

	@include bp($point_4 - 1) {
		display: none;
	}
}

.showcase__btn {
	@extend %btn_size_1;
	@extend %btn_theme_5;
}

.showcase__btn_icon {
	@extend %btn_icon_size_1;
	@extend %btn_icon_theme_2;
}

.showcase__img {
	position: relative;
	display: block;
	width: 100%;
	margin: auto;

	@include d3();

	@include bp($point_2, min) {
		max-width: 80.6rem;
	}

	@include bp($point_3, $point_4) {
		max-width: 56.6rem;
		margin-bottom: 2.2rem;
	}

	@include bp($point_4 - 1) {
		max-width: 28rem;
		margin-bottom: 2.2rem;
	}

	&:before {
		content: "";
		display: block;

		@include bp($point_2, min) {
			padding-bottom: 100%;
		}

		@include bp($point_3) {
			padding-bottom: 82%;
		}
	}

	&.showcase__img--mobile {
		@include bp($point_2, min) {
			display: none;
		}
	}
}

.showcase__img_in {
	@extend %contain_img;

	position: absolute;
	top: 0;
	left: 0;
}

// controls

.showcase__controls {
	@extend %global_flex_block_row_nowrap_flex-start_center;

	position: absolute;
	right: 0;

	z-index: 3;

	@include bp($point_2, min) {
		bottom: 6.4rem;
		width: 50%;
		padding: 0 var(--gl-indent) 0 8.2rem;
	}

	@include bp($point_3) {
		bottom: 6.2rem;
		width: 100%;
		padding: 0 var(--gl-indent);
	}
}

.showcase__progress {
	@extend %global_flex_block_row_nowrap_flex-start_center;

	@include bp($point_2, min) {
		margin-right: 2rem;
	}

	@include bp($point_3) {
		width: 100%;
	}
}

.showcase__progress_title {
	color: var(--white_color);

	&:last-child {
		margin: 0;
	}

	@include bp($point_2, min) {
		width: 3.4rem;
		margin-right: 1.4rem;
		font-size: 2.4rem;
	}

	@include bp($point_3, $point_4) {
		width: 2.2rem;
		margin-right: 1.2rem;
		font-size: 1.6rem;
	}

	@include bp($point_4 - 1) {
		width: 2rem;
		margin-right: 1.2rem;
		font-size: 1.3rem;
	}
}

.showcase__dots {
	@extend %global_flex_block_row_nowrap_flex-start;
	height: 2px;
	margin-right: 1.4rem;
	// background-color: rgba(#fff, 0.24);

	@include bp($point_2, min) {
		width: 26rem;
	}

	@include bp($point_3) {
		flex-grow: 1;
	}
}

.swiper-pagination-bullet,
.showcase__dot {
	position: relative;

	height: 100%;
	margin-right: .4rem;
	flex-grow: 1;

	overflow: hidden;
	cursor: pointer;
	
	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: var(--white_color);
		opacity: .24;

		pointer-events: none;
	}

	&:last-child {
		margin: 0;
	}
}

.showcase__dot_in {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: var(--white_color);

	pointer-events: none;
}

.showcase__arrows {
	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start, center, center);

		margin-left: auto;
	}

	@include bp($point_3) {
		display: none;
	}
}

.showcase__arrow {
	@extend %global_flex_block_row_nowrap_center_center;

	width: 4.6rem;
	height: 4.6rem;
	margin-right: 2rem;

	color: var(--primary_dark_color);

	background-color: var(--white_color);
	border-radius: 50%;

	transition: all 0.3s;
	cursor: pointer;

	&:hover {
		color: var(--white_color);
		background-color: var(--blue_color);
	}

	&:last-child {
		margin: 0;
	}
}

.showcase__arrow_icon {
	display: block;
	width: 1rem;
	height: 1.6rem;

	.showcase__arrow--prev & {
		transform: rotate(-180deg);
		margin-left: -0.3rem;
	}

	.showcase__arrow--next & {
		margin-left: 0.3rem;
	}
}
