@import "ScssHelpers/helpers_all";

.work_space {
	background: var(--primary_dark_color);

	@include bp($point_2, min) {
		padding-top: 21rem;
		padding-bottom: 26rem;
	}

	@include bp($point_3, $point_4) {
		padding-top: 9rem;
		padding-bottom: 10rem;
	}

	@include bp($point_4 - 1) {
		padding-top: 7rem;
		padding-bottom: 6rem;
	}
}

.work_space__wrap {
	@include bp($point_2, min) {
		display: grid;
		grid-template: 2fr 1fr / repeat(12, 1fr);
	}
}

.work_space__content {
	grid-area: span 1 / span 7;

	@include bp($point_2, min) {
		padding-right: 3rem;
	}

	@include bp($point_3, $point_4) {
		margin-bottom: 6.4rem;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 5.4rem;
	}
}

.work_space__title {
	@extend %h1;

	color: var(--white_color);

	span {
		font-family: $font_2;
		color: var(--dot-color);
	}

	@include bp($point_2, min) {
		margin-bottom: 6.6rem;
	}

	@include bp($point_3, $point_4) {
		margin-bottom: 4.6rem;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 4.2rem;
	}
}

.work_space__descrip {
	font-weight: 400;
	letter-spacing: -0.01em;

	color: var(--white_color);

	p {
		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}

	a{
		&:hover,
		&:focus {
			color: var(--orange_color);
			text-decoration: none;
		}
	}

	@include bp($point_2, min) {
		max-width: 77rem;
		font-size: 3.2rem;
		line-height: 1.38;
	}

	@include bp($point_3, $point_4) {
		max-width: 88%;
		font-size: 2.4rem;
		line-height: 1.42;
	}

	@include bp($point_4 - 1) {
		font-size: 1.8rem;
		line-height: 1.28;
	}
}

.work_space__img_w {
	@extend %global_flex_block_row_wrap_center_center;

	position: relative;

	grid-area: span 2 / span 5;

	@include bp($point_3, $point_4) {
		margin-bottom: 9.6rem;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 5.8rem;
	}
}

.work_space__img {
	@include bp($point_2, min) {
		position: absolute;
		top: -4.4rem;
		left: -8.4rem;
		bottom: -4.4rem;
		right: 0;
	}

	@include bp($point_3, $point_4) {
		max-width: 45rem;
		width: 100%;
	}

	@include bp($point_4 - 1) {
		max-width: 40rem;
		width: 90%;
	}
}

.work_space__img_in {
	// @extend %cover_img;
	display: block;
	width: 100%;
	height: 100%;
}

.work_space__list {
	display: flex;
	justify-content: flex-start;
	align-content: flex-end;
	align-items: stretch;
	flex-wrap: wrap;

	grid-area: span 1 / span 7;

	@include bp($point_2, min) {
		margin: 13.2rem -4rem -2rem 0;
	}

	@include bp($point_3, $point_4) {
		margin: 0 -5rem -2rem 0;
	}
}

.work_space__item {
	@include bp($point_2, min) {
		padding: 0 4rem 2rem 0;
	}

	@include bp($point_3, $point_4) {
		padding: 0 5rem 2rem 0;
	}

	@include bp($point_4, min) {
		flex: 0 1 33.333%;
	}

	@include bp($point_4 - 1) {
		width: 100%;

		&:not(:last-child) {
			margin-bottom: 3.6rem;
		}
	}
}

.work_space_item__title {
	font-weight: 700;
	letter-spacing: normal;
	color: var(--white_color);

	@include bp($point_2, min) {
		margin-bottom: 1.4rem;

		font-size: 2rem;
		line-height: 1.45;
	}

	@include bp($point_3, $point_4) {
		margin-bottom: 1rem;

		font-size: 1.8rem;
		line-height: 1.44;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 1.4rem;

		font-size: 1.4rem;
		line-height: 1.36;
	}
}

.work_space_item__descrip {
	font-weight: 500;
	letter-spacing: normal;
	color: var(--gray_color);

	@include bp($point_2, min) {
		font-size: 2rem;
		line-height: 1.45;
	}

	@include bp($point_3, $point_4) {
		font-size: 1.8rem;
		line-height: 1.44;
	}

	@include bp($point_4 - 1) {
		font-size: 1.4rem;
		line-height: 1.56;
	}
}
