@import "ScssHelpers/helpers_all";

.review {
	@extend %global_flex_block_column_nowrap_flex-start_stretch;

	margin: 0 auto;
	width: 100%;

	font-size: 1rem;

	background-color: var(--primary_dark_color);

	&:hover,
	&:focus {
		text-decoration: none;
	}
	@include bp($point_2, min) {
		padding: 3.6rem 3.6rem 3rem 3.6rem;
		min-height: 44.6rem;
	}

	@include bp($point_3, $point_4) {
		min-height: 31.2rem;
		padding: 2.4rem 2.4rem 2.6rem;
	}

	@include bp($point_4, min) {
		border-radius: 1rem;
	}

	@include bp($point_4 - 1) {
		padding: 2.4rem 2rem;
		min-height: 28rem;
	}
}

.review__head {
	@extend %global_flex_block_row_nowrap_flex-start_center;


	@include bp($point_2, min) {
		margin-bottom: 5.8rem;
	}

	@include bp($point_3, $point_4) {
		margin-bottom: 2.6rem;
	}

	@include bp($point_4, min) {
		padding-left: 1.2rem;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 2.8rem;
	}
}

.review__logo_title {
	font-weight: 500;
	letter-spacing: -.32px;
	color: var(--gray_color);

	@include bp($point_2, min) {
		font-size: 16px;
	}

	@include bp($point_3, $point_4) {
		font-size: 1.1rem;
	}

	@include bp($point_4, min) {
		margin-bottom: 1rem;
	}

	@include bp($point_4 - 1) {
		margin-bottom: .6rem;
		font-size: 1rem;
	}
}

.review__logo_block {
	@include bp($point_2, min) {
		max-width: 11.2rem;
	}

	@include bp($point_3, $point_4) {
		max-width: 7.8rem;
	}

	@include bp($point_4 - 1) {
		max-width: 7rem;
	}
}

.review__logo_img {
	@extend %contain_img;
}

.review__arrow {
	@extend %global_flex_block_row_nowrap_center_center;

	margin-left: auto;

	background-color: var(--white_color);
	border-radius: 50%;

	@include bp($point_2, min) {
		width: 7.6rem;
		height: 7.6rem;
	}

	@include bp($point_3, $point_4) {
		width: 5.4rem;
		height: 5.4rem;
	}

	@include bp($point_4 - 1) {
		width: 4.8rem;
		height: 4.8rem;
	}
}

.review__arrow_icon {
	@include bp($point_2, min) {
		width: 2.2rem;
		height: 2.2rem;
	}

	@include bp($point_3, $point_4) {
		width: 1.54rem;
		height: 1.54rem;
	}

	@include bp($point_4 - 1) {
		width: 1.4rem;
		height: 1.4rem;
	}
}

.review__main {
	margin-bottom: 2rem;
}


.review__score {
  font-weight: 500;
	line-height: 1;
	color: var(--white_color);
	text-align: center;

	@include bp($point_2, min) {
		font-size: 8.9rem;
		letter-spacing: -.7rem;
	}

	@include bp($point_3, $point_4) {
		font-size: 6.2rem;
		letter-spacing: -.4rem;
	}

	@include bp($point_4 - 1) {
		font-size: 5.4rem;
		letter-spacing: -.3rem;
	}
}

.review__stars {
	max-width: 100%;

	@include bp($point_2, min) {
		width: 22.8rem;
		margin: 0 auto 2rem;
	}

	@include bp($point_3, $point_4) {
		width: 16rem;
		margin: 0 auto 1.2rem;
	}

	@include bp($point_4 - 1) {
		width: 13.8rem;
		margin: 0 auto 1.4rem;
	}
}

.review__stars_img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: contain;
}

.review__info {
  font-weight: 500;
  letter-spacing: -.4px;
  color: var(--gray_color);
	text-align: center;
	text-transform: uppercase;

	@include bp($point_2, min) {
		font-size: 1.6rem;
	}

	@include bp($point_3, $point_4) {
		font-size: 1.1rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1rem;
	}
}

.review__verified {
	@extend %global_flex_block_row_nowrap_center_center;
	margin-top: auto;
	color: var(--green_color);
}

.review__verified_icon {
	@include bp($point_2, min) {
		width: 1.8rem;
		height: 1.8rem;
		margin-right: .8rem;
	}

	@include bp($point_3) {
		width: 1.2rem;
		height: 1.2rem;
		margin-right: .6rem;
	}
}

.review__verified_title {
	@include bp($point_2, min) {
		font-size: 1.8rem;
	}

	@include bp($point_3, $point_4) {
		font-size: 1.2rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1.3rem;
	}
}