@import 'ScssHelpers/_helpers_all.scss';

.contact_us {
	padding: 0;
}

.contact_us__in {
	@extend %global_flex_block_row_wrap_flex-start_stretch;

	align-content: stretch;

	@include bp($point_2, min) {
		min-height: 56.25vw;
	}

	@include bp($point_3, $point_4) {
		min-height: 74.34vw;
	}

	@include bp($point_4 - 1) {
		min-height: none;
	}
}

.contact_us__text {
	@extend %global_flex_block_column_nowrap_center;

	@include bp($point_4, min) {
		padding: var(--gl-indent);
	}

	@include bp($point_4 - 1) {
		justify-content: flex-start;
		padding: 3.8rem var(--gl-indent) 6.4rem;
		width: 100%;
	}
}

.contact_us__subtitle {
	color: var(--gray_color);
	letter-spacing: -.01em;

	@include bp($point_2, min) {
		margin-bottom: 7rem;

		font-size: 2.8rem;
		line-height: 1.28;
	}

	@include bp($point_3, $point_4) {
		margin-bottom: 2.4rem;

		font-size: 1.4rem;
		line-height: 1.42;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 3.6rem;

		font-size: 2rem;
		line-height: 1.32;
	}
}

.contact_us__title {
	@extend %h1;

	letter-spacing: -0.06em;

	color: var(--primary_dark_color);

	@include bp($point_2, min) {
		margin-bottom: 12rem;
		max-width: 68rem;
	}

	@include bp($point_3, $point_4) {
		margin-bottom: 4.2rem;
		max-width: 31rem;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 4.8rem;
		max-width: 23rem;
	}
}

.contact_us__cta_icon_and_text {
	@extend %global_flex_block_row_nowrap_flex-start;

	@include bp($point_2, min) {
		margin-bottom: 10rem;
	}

	@include bp($point_3, $point_4) {
		margin-bottom: 5rem;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 2.4rem;
	}
}

.contact_us__cta_icon {
	width: 2.8rem;
	height: 2.8rem;

	@include bp($point_2, min) {
		margin-right: 3rem;
	}

	@include bp($point_3, $point_4 - 1) {
		margin-right: 2rem;
	}

	@include bp($point_4 - 1) {
		margin-right: 3rem;
	}
}

.contact_us__cta_text {
	color: var(--primary_color);
	font-weight: 700;

	@include bp($point_2, min) {
		font-size: 2rem;
		line-height: 1.35;
	}

	@include bp($point_3, $point_4) {
		font-size: 1.8rem;
		line-height: 1.44;
	}

	@include bp($point_4 - 1) {
		position: relative;
		top: -6px;

		font-size: 1.4rem;
		line-height: 1.36;
	}
}

.contact_us__button {
	@extend %btn_size_1;
	@extend %btn_theme_2;

	@include bp($point_4 - 1) {
		align-self: center;
	}
}

.contact_us__button_icon {
	@extend %btn_icon_size_1;
	@extend %btn_icon_theme_1;
}

.contact_us__img_wrap {
	margin-left: auto;

	@include bp($point_2, min) {
		width: 50%;
	}

	@include bp($point_3, $point_4) {
		width: 46.62%;
	}

	@include bp($point_4 - 1) {
		width: 100%;
		height: 100vw;
	}

	@include bp ($point_4, min) {
		flex-shrink: 0;
	}

	@include bp($point_4 - 1) {
		flex-shrink: 1;
	}
}

.contact_us__picture {
	display: block;
	width: 100%;
	height: 100%;
}

.contact_us__img {
	@extend %cover_img;
}