@import "ScssHelpers/_helpers_all.scss";

.features_section {
	background-color: var(--light_color);

	@include bp($point_2, min) {
		padding-top: 18.8rem;
		padding-bottom: 18.2rem;
	}

	@include bp($point_3, $point_4) {
		padding-top: 10.4rem;
		padding-bottom: 9.7rem;
	}

	@include bp($point_4 - 1) {
		padding-top: 5.9rem;
		padding-bottom: 6.4rem;
	}
}

.features_section_title {
	@extend %h1;

	color: var(--primary_dark_color);

	@include bp($point_2, min) {
		max-width: 74rem;

		&:not(:last-child) {
			margin-bottom: 11.9rem;
		}
	}
	@include bp($point_3, $point_4) {
		max-width: 34rem;

		&:not(:last-child) {
			margin-bottom: 8.8rem;
		}
	}

	@include bp($point_4 - 1) {
		max-width: 24.4rem;

		&:not(:last-child) {
			margin-bottom: 4.4rem;
		}
	}
}

.features_section_title__dott {
	display: inline-block;

	font-family: "Arial";
	color: var(--dot-color);
}

.features_section__list {
	@include bp($point_2, min) {
		margin: 0 -5rem -10rem 0;
	}

	@include bp($point_4, min) {
		@include flex_block(row, wrap, flex-start, flex-start, normal);
	}

	@include bp($point_3, $point_4) {
		margin: 0 -5.2rem -9.8rem 0;
	}
}

.features_section_item {
	@include bp($point_2, min) {
		flex: 0 1 25%;
		max-width: 25%;

		padding: 0 13.3rem 10rem 0;
	}

	@include bp($point_3, $point_4) {
		flex: 0 1 50%;
		max-width: 50%;

		padding: 0 12rem 9.9rem 0;
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 5.7rem;
		}
	}
}

.features_section_item__wrap {
	border-radius: 1rem;

	@include bp($point_2, min) {
		width: 20rem;
		height: 20rem;

		&:not(:last-child) {
			margin-bottom: 3.2rem;
		}
	}

	@include bp($point_3, $point_4) {
		width: 25.2rem;
		height: 25.2rem;

		&:not(:last-child) {
			margin-bottom: 3.3rem;
		}
	}

	@include bp($point_4 - 1) {
		width: 13.2rem;
		height: 13.2rem;

		margin-left: auto;
		margin-right: auto;

		&:not(:last-child) {
			margin-bottom: 2.9rem;
		}
	}
}

.features_section_item__img {
	width: 100%;
	height: 100%;

	@extend %contain_img;
}

.features_section_item__content {
	@include bp($point_4 - 1) {
		margin-left: auto;
		margin-right: auto;

		text-align: center;
	}
}

.features_section_item__content--common {
	@include bp($point_2, min) {
		max-width: 31.3rem;
	}

	@include bp($point_3, $point_4) {
		max-width: 26rem;
	}

	@include bp($point_4 - 1) {
		max-width: 22.2rem;
	}
}

.features_section_item__content--thinner {
	@include bp($point_2, min) {
		max-width: 27rem;
	}

	@include bp($point_3, $point_4) {
		max-width: 23rem;
	}

	@include bp($point_4 - 1) {
		max-width: 20rem;
	}
}

.features_section_item__title {
	color: var(--primary_color);
	font-weight: 700;
	font-family: $biotif_font;
	letter-spacing: -0.034em;

	@include bp($point_2, min) {
		font-size: 3rem;
		line-height: 1.23;

		&:not(:last-child) {
			margin-bottom: 2.8rem;
		}
	}

	@include bp($point_3, $point_4) {
		font-size: 2.8rem;
		line-height: 1.29;

		&:not(:last-child) {
			margin-bottom: 1.4rem;
		}
	}

	@include bp($point_4 - 1) {
		font-size: 1.9rem;

		max-width: 20.5rem;

		&:not(:last-child) {
			margin-bottom: 1.6rem;
		}
	}
}

.features_section_item__descr {
	color: var(--gray_color);
	font-family: $biotif_font;

	@include bp($point_2, min) {
		font-size: 2rem;
		line-height: 1.5;
	}

	@include bp($point_3, $point_4) {
		font-size: 1.6rem;
		line-height: 1.375;
	}

	@include bp($point_4 - 1) {
		font-size: 1.4rem;
	}
}
