@import "ScssHelpers/helpers_all";
//---------------------------------------------testimonial_slider
.testimonial_slider {
	position: relative;

	// max-width: 104.4rem;
	// width: 100%;

	overflow: hidden;

	&.swiper-initialized {
		overflow: visible !important;
	}
}

.testimonial_slider__controls {
	position: absolute;
	top: 0;
	z-index: 2;

	// padding-left: 6.4rem;

	left: var(--testimonial-info-col-width);
	width: calc(100% - var(--testimonial-info-col-width));

	@include bp($point_2, min) {
		height: 9.6rem;
		padding-left: 10%;
	}

	@include bp($point_3, $point_4) {
		height: 5.8rem;
		padding-left: 5.6rem;
	}

	@include bp($point_4 - 1) {
		display: none;
	}
}

.testimonial_slider__controls_in {
	@include flex_block(row, wrap, flex-start);

	position: relative;

	height: 100%;

	border-bottom: solid 1px #b7becc;

	@include bp($point_2, min) {
		align-content: center;
	}

	&:before {
		position: absolute;
		left: 0;

		pointer-events: none;

		background: url("../../images/quote_decor.png") no-repeat center center;
		background-size: contain;

		content: "";

		@include bp($point_2, min) {
			top: 2.4rem;

			width: 5.8rem;
			height: 4.6rem;
		}

		@include bp($point_3) {
			top: 0;

			width: 4rem;
			height: 4rem;
		}

		@include bp($point_4 - 1) {
			display: none;
		}
	}
}
//---------------------------------------------testimonial_slider###

.testimonial_slider__pagination {
	@extend %global_flex_block_row_nowrap_flex-start_center;

	position: absolute;
	bottom: 0;

	width: 100%;

	@include bp($point_4, min) {
		display: none;
	}
}

.testimonial_slider__pagination_dots {
	@extend %global_flex_block_row_nowrap_flex-start;

	flex-grow: 1;
	height: 0.2rem;

	margin: 0 1.2rem;

	background-color: var(--border_grey_color);

	.swiper-pagination-progressbar-fill {
		flex-grow: 1;
		height: inherit;

		transform-origin: left top;
		background-color: var(--blue_color);
	}
}

.testimonial_slider__pagination_count {
	font-size: 1.3rem;
	color: var(--blue_color);
}
