@import "ScssHelpers/_helpers_all.scss";

.what_to_read {
	background-color: var(--green_color);

	@include bp($point_2, min) {
		padding-top: 8.2rem;
		padding-bottom: 18rem;
	}
	@include bp($point_3, $point_4) {
		padding-top: 4.1rem;
		padding-bottom: 11.6rem;
	}

	@include bp($point_4 - 1) {
		padding-top: 4.1rem;
		padding-bottom: 4.4rem;
		padding-right: 1.6rem;
	}
}

.what_to_read__title {
	@extend %h4;

	margin: 0 auto 6.4rem 0;

	color: var(--white_color);

	@include bp($point_3) {
		font-size: 1.4rem;
		margin: 0 auto 9.3rem;
	}
	@include bp($point_4 - 1) {
		display: none;
	}
}

.what_to_read__content {
	margin: 0 auto;

	@include bp($point_2, min) {
		padding-left: 22rem;
		max-width: 120rem;
		@include flex_block(row, wrap, space-between, center, center);
	}

	@include bp($point_1, $point_2) {
		padding-left: 0;
	}

	@include bp($point_3, $point_4) {
		max-width: 47rem;
	}

	p {
		@extend %h3;

		font-weight: 400;

		color: var(--white_color);

		@include bp($point_4, min) {
			font-size: 2.8rem;
		}

		@include bp($point_4 - 1) {
			font-size: 1.9rem;
		}
	}

	p strong {
		@extend %h3;

		color: var(--white_color);

		&:hover,
		&:focus {
			text-decoration: none;
		}

		@include bp($point_4, min) {
			font-size: 2.8rem;
			letter-spacing: -0.023em;
		}

		@include bp($point_4 - 1) {
			letter-spacing: -0.02em;
		}
	}
}

.what_to_read__list {
	position: relative;

	&:before {
		content: "";
		position: absolute;
		left: 0;
		top: 50%;

		width: 5.5rem;
		height: 100%;

		transform: translateY(-50%);

		background-image: url(../../images/bracket-icon.svg);
		background-size: contain;
		background-repeat: no-repeat;
		background-position: center;
	}

	@include bp($point_2, min) {
		padding: 4.6rem 0 5.5rem 11rem;
	}

	@include bp($point_3, $point_4) {
		margin-bottom: 7.8rem;
		padding: 4rem 0 4.2rem 11rem;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 4rem;
		padding: 1.4rem 0 1.6rem 4.7rem;

		&:before {
			left: -1.3rem;
		}
	}
}

.what_to_read__item {
	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-bottom: 1rem;
		}
	}
	@include bp($point_3, $point_4) {
		&:not(:last-child) {
			margin-bottom: 0.7rem;
		}
	}
	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 0.4rem;
		}
	}
}

.what_to_read__item_title {
	@extend %h3;

	color: var(--white_color);

	&:hover,
	&:focus {
		text-decoration: none;
	}

	@include bp($point_4, min) {
		font-size: 2.8rem;
		letter-spacing: -0.023em;
	}

	@include bp($point_4 - 1) {
		letter-spacing: -0.02em;
	}
}

.what_to_read__item_text {
	@extend %h3;

	font-weight: 400;

	color: var(--white_color);

	@include bp($point_4, min) {
		font-size: 2.8rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1.9rem;
	}
}

.what_to_read__btn_w {
	@include bp($point_3) {
		text-align: center;
	}
}

.what_to_read__btn {
	@extend %btn_size_1;
	@extend %btn_theme_3;
}

.what_to_read__btn_icon {
	@extend %btn_icon_size_1;
	@extend %btn_icon_theme_1;

	transition: color 0.3s ease, background-color 0.3s ease;

	.what_to_read__btn:hover & {
		color: var(--white_color);
	}

	.what_to_read__btn:hover & {
		background-color: var(--green_color);
	}
}
