@import "ScssHelpers/_helpers_all.scss";

.dev_partner {
	padding: 0;

	@include bp($point_2, min) {
		min-height: 108rem;
	}

	@include bp($point_4, min) {
		background-color: var(--light_blue);
	}

	@include bp($point_4 - 1) {
		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			z-index: -1;

			width: 100%;
			height: 50%;
			background-color: var(--light_blue);
		}

		&::after {
			content: "";
			position: absolute;
			bottom: 0;
			left: 0;
			z-index: -1;

			width: 100%;
			height: 50%;
			background-color: var(--light_color);
		}
	}
}

.dev_partner__main {
	@include bp($point_2, min) {
		box-sizing: content-box;
		padding: 29.8rem 0 0 var(--gl-indent);
		max-width: 43rem;
	}

	@include bp($point_3) {
		padding: 10rem var(--gl-indent) 11rem;
	}

	@include bp($point_4 - 1) {
		padding: 6.4rem var(--gl-indent);

		@include flex_block(column, nowrap, flex-start);
	}
}

.dev_parter__title {
	font-weight: 600;
	line-height: 1.04;
	color: var(--primary_light_color);
	letter-spacing: -0.02em;

	@include bp($point_2, min) {
		font-size: 4.8rem;

		&:not(:last-child) {
			margin-bottom: 3.4rem;
		}
	}

	@include bp($point_3, $point_4) {
		font-size: 4.4rem;

		&:not(:last-child) {
			margin-bottom: 3.6rem;
		}
	}

	@include bp($point_4 - 1) {
		font-size: calc(0.0446 * 100vw + 1rem);

		&:not(:last-child) {
			margin-bottom: 1.8rem;
		}
	}
}

.dev_parter__subtitle {
	font-weight: 500;
	line-height: 1.45;
	color: var(--gray_color);

	a {
		text-decoration: none;

		&:hover, &:active, &:focus {
			color: var(--orange_color);
			text-decoration: none;
		}
	}

	@include bp($point_2, min) {
		font-size: 2rem;

		&:not(:last-child) {
			margin-bottom: 5.2rem;
		}
	}

	@include bp($point_3, $point_4) {
		max-width: 42.8rem;

		font-size: 1.8rem;

		&:not(:last-child) {
			margin-bottom: 5.2rem;
		}
	}

	@include bp($point_4 - 1) {
		max-width: calc(0.384 * 100vw + 13.6rem);

		font-size: calc(0.0089 * 100vw + 1.1rem);

		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}
}

.dev_partner__grid {
	display: grid;

	@include bp($point_2, min) {
		position: absolute;
		top: 0;
		right: 0;

		grid-template: repeat(3, 36rem) / repeat(4, 36rem);
	}

	@include bp($point_3, $point_4) {
		grid-template-columns: repeat(2, 1fr);
		margin-bottom: 5.6rem;
	}

	@include bp($point_4 - 1) {
		position: relative;
		left: calc(-1 * var(--gl-indent));

		margin-bottom: 4.4rem;
		width: 100vw;
	}
}

.dev_partner__grid_item {
	@include bp($point_3) {
		position: relative;

		padding-top: 100%;
	}

	&.dev_partner__grid_item--big_img_mod {
		@include bp($point_2, min) {
			grid-area: 1 / 3 / span 2 / span 2;
		}

		@include bp($point_3, $point_4) {
			display: none;
		}

		@include bp($point_4 - 1) {
			grid-area: 3 / 1 / span 2 / span 2;
		}
	}

	&.dev_partner__grid_item--orange_mod {
		@include bp($point_2, min) {
			grid-area: 2 / 2 / span 1 / span 1;
		}

		@include bp($point_3) {
			grid-area: 2 / 1 / span 1 / span 1;
		}
	}

	&.dev_partner__grid_item--white_mod {
		@include bp($point_9, min) {
			grid-area: 3 / 1 / span 1 / span 1;
		}

		@include bp($point_9 - 1, $point_2) {
			grid-area: 3 / 2 / span 1 / span 1;
		}

		@include bp($point_3) {
			grid-area: 1 / 1 / span 1 / span 1;
		}
	}

	&.dev_partner__grid_item--small_img_mod {
		@include bp($point_9, min) {
			grid-area: 3 / 2 / span 1 / span 1;
		}

		@include bp($point_9 - 1, $point_2) {
			display: none;
		}
	}

	&.dev_partner__grid_item--primary_dark_mod {
		@include bp($point_2, min) {
			grid-area: 3 / 3 / span 1 / span 1;
		}
	}

	&.dev_partner__grid_item--primary_light_mod {
		@include bp($point_2, min) {
			grid-area: 3 / 4 / span 1 / span 1;
		}

		@include bp($point_3) {
			display: none;
		}
	}
}

.dev_partner__cube {
	width: 100%;
	height: 100%;
	overflow: hidden;

	@include bp($point_3) {
		position: absolute;
		top: 0;
		left: 0;
	}
}

.dev_partner__button {
	@extend %btn_size_1;
	@extend %btn_theme_2;

	@include bp($point_4 - 1) {
		align-self: center;
	}
}

.dev_partner__button_text {
	&:hover,
	&:focus {
		text-decoration: none;
	}
}

.dev_partner__button_icon {
	@extend %btn_icon_size_1;
	@extend %btn_icon_theme_1;
}
