@import "ScssHelpers/helpers_all";

.logos {
	margin: 0 calc(var(--gl-indent) * -1);

	overflow: hidden;

	// @include bp($point_2, min) {
	// 	margin-bottom: 14rem;
	// }

	// @include bp($point_3, $point_4) {
	// 	margin-bottom: 7.4rem;
	// }

	// @include bp($point_4 - 1) {
	// 	margin-bottom: 5.6rem;
	// }

	.swiper-wrapper{
		transition-timing-function: linear !important; 
	}
}

.logo__slider {
	width: 100%;
}

.logo__slider_item {
	position: relative;

	&:before {
		content: '';

		display: block;
		padding-top: 100%;
	}

	&.swiper-slide {
		height: auto;

		@include bp($point_1 + 81, min) {
			width: 32rem;
		}

		@media (min-width: $point_4 + 1) and (max-width: $point_1 + 80) {
			width: 21.4rem;
		}

		@media (min-width: $point_6 + 1) and (max-width: $point_4) {
			width: 17.8rem;
		}

		@include bp($point_6) {
			width: 13rem;
		}
	}
}

.logo_item {
	@extend %global_flex_block_row_wrap_center_center;

	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;
	padding: 1rem;

	background-color: var(--white_color);

	border: 1px solid var(--border_grey_color);

	.logo__slider_item:not(:first-child) & {
		border-left: 0;
	}

	&:hover,
	&:focus {
		text-decoration: none;
	}
}

.logo_item__img {
	@extend %global_flex_block_row_wrap_center_center;

	position: relative;

	width: 68.8%;
	height: 12.5%;
}

.logo_img {
	@extend %transition_opacity;

	position: absolute;

	max-width: 100%;
	max-height: 100%;
	object-fit: contain;

	&.logo_img--hover_state {
		@include bp($point_2, min) {
			opacity: 0;
		}

		@include bp($point_3) {
			opacity: 1;
		}
	}

	&:not(.logo_img--hover_state) {
		@include bp($point_3) {
			opacity: 0;
		}
	}

	@include bp($point_2, min) {
		.logo_item:hover &,
		.logo_item:focus {
			&:not(.logo_img--hover_state) {
				opacity: 0;
			}
			
			&.logo_img--hover_state {
				opacity: 1;
			}
		}
	}
}
