@import "ScssHelpers/helpers_all";
//---------------------------------------------slider_controls
.slider_controls {
	@include flex_block(row, wrap, flex-start, center);

	&.slider_controls--align_mod {
		margin-left: auto;
	}
}

.slider_controls__button {
	display: inline-flex;
	align-items: center;
	justify-content: center;

	color: var(--white_color);

	background-color: var(--blue_color);
	transition: background-color 0.3s ease;
	border-radius: 50%;
	overflow: hidden;

	cursor: pointer;

	@include bp($point_2, min) {
		width: 4.8rem;
		height: 4.8rem;
	}

	@include bp($point_3, $point_4) {
		width: 4rem;
		height: 4rem;
	}

	&:not(:last-of-type) {
		margin-right: 1.8rem;
	}

	&.swiper-button-disabled {
		cursor: default;
	}

	&:hover {
		color: var(--primary_dark_color);

		background-color: var(--white_color);
	}
}

.slider_controls__button_text {
	position: absolute;
	width: 1px;
	height: 1px;
	padding: 0;
	margin: -1px;
	overflow: hidden;
	clip: rect(0, 0, 0, 0);
	white-space: nowrap;
	border: 0;
}

.slider_controls__icon {
	display: block;
	width: 0.8rem;
	height: 1.4rem;

	.swiper-button-next & {
		margin-right: -0.3rem;
	}
	.swiper-button-prev & {
		transform: rotateZ(180deg);
		margin-left: -0.3rem;
	}
}
//---------------------------------------------slider_controls###
